$( document ).ready( function( ) {
    var nav_beta = $( '#navigationBeta' ),
        distrib_list = $( '#body-content .distributorList' ),
        null_msg = $( '<h2 />' ).addClass( 'no-distribuors' ).text( 'There are no distributors for this region.' );

    null_msg.insertBefore( distrib_list ).hide( );

    if (!/international-resources/.test( location.pathname )) {
        // suppress this control from international resources page
        nav_beta.find( 'ul:first' ).append($( '<li><a href="#" id="region_all">View All</a></li>' ));
    }

    nav_beta.on( 'click', 'li > a[id^="region"]', function( e ) {
        e.preventDefault( );
        var region_id = e.target.id,
            dis = distrib_list.find( 'address' );

        nav_beta.find( 'li' ).removeClass( 'current' );

        $( e.target ).parent( 'li' ).addClass( 'current' );

        null_msg.hide( );

        if ( region_id == 'region_all' ) {
            dis.fadeIn( 400 );
        } else {
            dis.hide( );
            var sel_region = dis.filter( '.' + region_id );

            if ( sel_region.length > 0 ) {
                distrib_list.prepend(sel_region[0]); // patch: makes item first-child to fix removing unwanted border-top
                sel_region.fadeIn( 400 );
            } else {
                null_msg.fadeIn( 400 );
            }
        }

        e.preventDefault( );
    });
});



